class Resources {
  constructor(values = {}) { this.values = values }
  getValues() { return this.values }
  getValue(resName) { return _.get(this.values, resName, 0.0) }
  add(resources) {
    let changedValues = {}
    for (const [key, value] of _.entries(resources.getValues())) {
      changedValues[key] = this.getValue(key) + value
    }
    return new Resources({ ...this.values, ...changedValues })
  }
  sub(resources) {
    let changedValues = {}
    for (const [key, value] of _.entries(resources.getValues())) {
      changedValues[key] = this.getValue(key) - value
    }
    return new Resources({ ...this.values, ...changedValues })
  }
  set(resources) {
    for (const [key, value] of _.entries(resources.getValues())) {
      this.values[key] = value
    }
    return this
  }
}

const calcOutputs = ({ resources, slider, health, strength }) => {
  const values = _.entries(resources)
    .reduce((acc, [resource, quantity]) => ({ ...acc, [resource]: strength * quantity * slider * 0.4/0.3 * health }), {})
  return new Resources(values)
}

const calcInputs = ({ resources, slider, health, strength }) => {
  const values = _.entries(resources)
    .reduce((acc, [resource, quantity]) => ({ ...acc, [resource]: strength * quantity * health }), {})
  return new Resources(values)
}

export const performSimulation = ({ cards, itemsConfig }) => {
  const cardsByType = _.groupBy(cards, card => card.name)

  const items = _.keys(itemsConfig).map(name => {
    const cards = _.get(cardsByType, name, [])
    const strength = cards.reduce((acc, card) => acc + card.strength, 0)

    const { inputs, outputs, slider, health } = itemsConfig[name]

    return {
      name,
      strength,
      inputs: calcInputs({ resources: inputs, slider, health, strength }),
      outputs: calcOutputs({ resources: outputs, slider, health, strength })
    }
  })

  let balance = new Resources()

  console.time('simulation')
  const processedItems = items.map(item => {
    const { inputs, outputs } = item
    let usedResources = new Resources()
    let producedResources = new Resources()

    balance = balance.sub(inputs).add(outputs)
    usedResources = usedResources.sub(inputs)
    producedResources = producedResources.add(outputs)

    return {
      ...item,
      producedResources,
      usedResources,
      producedResources,
      currentResources: balance
    }
  })

  console.timeEnd('simulation')

  const usedStrength = processedItems.filter(item => item.used).reduce((sum, item) => sum + item.strength, 0)
  const totalStrength = processedItems.reduce((sum, item) => sum + item.strength, 0)

  return {
    items: processedItems,
    balance,
    usedStrength,
    totalStrength
  }
}
