import _ from 'lodash'
import { JsonRpc } from "@eoscafe/hyperion"

//https://hyperion.docs.eosrio.io/endpoint/#wax
export const endpoints = [
  'https://wax.eosrio.io',
  'https://wax.blacklusion.io',
  'https://wax.eu.eosamsterdam.net',
  'https://wax.eosphere.io',
  'https://wax.blokcrafters.io',
]

let currentEndpointIdx = 0

export const getHyperionRpc = () => {
  const endpoint = endpoints[currentEndpointIdx]
  const rpc = new JsonRpc(endpoint, { fetch })
  currentEndpointIdx = (currentEndpointIdx + 1) % endpoints.length
  return rpc
}
