import { useState, useEffect } from 'react'
import _ from 'lodash'
import Box from '@mui/material/Box'
import AskWallet from '../../components/AskWallet.js'
import { getHyperionRpc } from '../../lib/hyperion.js'
import { sleep } from '../../lib/utils.js'
import { DateTime } from 'luxon'

const ExpeditionProfits = () => {
  const [wallet, setWallet] = useState()
  const [games, setGames] = useState([])
  const [rewards, setRewards] = useState([])

  const fetchGames = () => {
    const hyperion = getHyperionRpc()

    hyperion.get_actions(wallet, {
      "act.name": 'transfer',
      "act.account": 'martia',
      "transfer.from": wallet,
      "transfer.to": 'game.mars',
      limit: 1000
    }).then(response => {
      const games = response.actions.map(action => {
        return {
          id: action.act.data.memo,
          date: action['@timestamp'],
          price: action.act.data.amount
        }
      })
      setGames(games)
    })
  }

  const fetchRewards = async () => {
    const sortedGames = _.sortBy(games, game => {
      return DateTime.fromISO(game.date).toUnixInteger()
    })

    const firstGame = _.first(sortedGames)
    const lastGame = _.last(sortedGames)

    const after = DateTime.fromISO(firstGame.date)
    const before = DateTime.fromISO(lastGame.date).plus({ minutes: 10 })

    // console.log(sortedGames)

    const limit = 1000
    let page = 0
    let actions = []
    while(true) {
      const hyperion = getHyperionRpc()
      let response
      try {
        response = await hyperion.get_actions('economy.mars', {
          after: after.toISO(),
          before: before.toISO(),
          'act.name': 'issue',
          'act.account': 'economy.mars',
          'act.authorization.actor': 'gamerwd.mars',
          track: true,
          limit,
          skip: page * limit
        })
      } catch (error) {
        console.error(error)
        continue
      }
      if (response.actions.length === 0) {
        break
      }

      const filteredActions = response.actions.filter(action => action.act.data.to === wallet)
      actions.push(...filteredActions)
      page += 1
    }

    setRewards(actions.map(action => action.act.data.quantity))
  }

  useEffect(() => {
    if (wallet) fetchGames()
  }, [wallet])

  useEffect(() => {
    fetchRewards()
  }, [games])

  const allMartiaSpent = games.reduce((acc, game) => {
    acc += game.price
    return acc
  }, 0)

  const allRewards = rewards.reduce((acc, reward) => {
    const [amount, symbol] = _.split(reward, ' ')

    if (acc[symbol] === undefined) {
      acc[symbol] = 0.0
    }

    acc[symbol] += parseFloat(amount)
    return acc
  }, {})

  return <Box>
    <AskWallet setWallet={setWallet} />
    <Box>All martia spent { allMartiaSpent.toFixed(2) }</Box>
    {
      _.entries(allRewards).map(([key, amount]) => (
        <Box key={key}>{ key } { amount.toFixed(2) }</Box>
      ))
    }

    {/* <table style={{ border: '1px solid black' }}> */}
    {/*   <thead> */}
    {/*     <tr> */}
    {/*       <th>ID</th> */}
    {/*       <th>Date</th> */}
    {/*       <th>Price</th> */}
    {/*     </tr> */}
    {/*   </thead> */}
    {/*   <tbody> */}
    {/*     { */}
    {/*       games.map(game => ( */}
    {/*         <tr key={game.id}> */}
    {/*           <td>{ game.id }</td> */}
    {/*           <td>{ game.date }</td> */}
    {/*           <td>{ game.price}</td> */}
    {/*         </tr> */}
    {/*       )) */}
    {/*     } */}
    {/*   </tbody> */}
    {/* </table> */}
  </Box>
}

export default ExpeditionProfits
