import _ from 'lodash'
import { Chip, Alert, Card, Box, Button, Paper, Typography, TextField, InputLabel, Select, MenuItem } from '@mui/material'
import { useEffect, useState, useMemo, useContext } from 'react'
import { LoaderContext } from '../../components/Loader.js'
import { useSnackbar } from 'notistack';

const ATOMIC_URL = "https://wax.api.atomicassets.io/atomicassets/v1"

const sleep = (ms) => {
  return new Promise(resolve => setTimeout(resolve, ms))
}

const fetchWithRetry = async (...args) => {
  const maxRetry = 10

  for(let retry = 0; retry < maxRetry - 1; retry++) {
    try {
      return await fetch(...args)
    } catch (error) {
      await sleep(1000)
    }
  }

  return await fetch(...args)
}

const getAssets = async ({ userAccount, rarity, schemaName }) => {
  const query = {
    owner: userAccount,
    collection_name: 'colonizemars',
    schema_name: schemaName,
    page: 1,
    limit: 1000,
    'data:text.rarity': rarity
  }
  const qs = new URLSearchParams(query)

  const resp = await fetchWithRetry(`${ATOMIC_URL}/assets?${qs}`)
  return (await resp.json()).data
}

const getClaims = async ({ rpc, accountName }) => {
  const result = await rpc.get_table_rows({
    code: 'colonizepool',
    table: 'claims',
    scope: 'colonizepool',
    key_type: 'name',
    index_position: 2,
    lower_bound: accountName,
    upper_bound: accountName,
    limit: 1000
  })
  return result.rows
}

const getTradeUps = async ({ rpc }) => {
  const result = await rpc.get_table_rows({
    code: 'colonizeupgr',
    table: 'tradeups',
    scope: 'colonizeupgr',
    limit: 1000
  })
  return result.rows
}

const getUpgrades = async ({ wax, assets }) => {
  const tradeUps = await getTradeUps({ rpc: wax.api.rpc })
  const groupedAssets = _.groupBy(assets, (asset) => asset.template.template_id)
  const activeTradeUps = tradeUps.filter(tradeUp => tradeUp.end_time === 0)

  let upgrades = []

  for (const tradeUp of activeTradeUps) {
    const templateId = _.get(tradeUp, 'input_slots.0.template_ids.0')
    if (!templateId) continue
    const group = groupedAssets[templateId]
    if (!group) continue

    const cardsNeeded = tradeUp.input_slots.length

    for (const chunk of _.chunk(group, cardsNeeded)) {
      if (chunk.length === cardsNeeded) {
        upgrades.push({
          assets: chunk,
          templateId,
          memo: tradeUp.tradeup_name
        })
      }
    }
  }

  return upgrades
}

const feePaymentAction = (userAccount) => {
  return {
    account: 'eosio.token',
    name: 'transfer',
    authorization: [{
      actor: userAccount,
      permission: 'active',
    }],
    data: {
      from: userAccount,
      to: 'gogobroccoli',
      quantity: '1.00000000 WAX',
      memo: 'broccoli-mars.github.io'
    },
  }
}

const applyUpgrades = async ({ upgrades, wax }) => {
  const actions = await Promise.all(upgrades.map(async (upgrade) => {
    const { assets, templateId, memo } = upgrade

    return {
      account: 'atomicassets',
      name: 'transfer',
      authorization: [{
        actor: wax.userAccount,
        permission: 'active',
      }],
      data: {
        from: wax.userAccount,
        to: 'colonizeupgr',
        asset_ids: assets.map(asset => asset.asset_id),
        memo,
      },
    }
  }))

  return await wax.api.transact({
    actions: [
      feePaymentAction(wax.userAccount),
      {
        account: 'res.pink',
        name: 'noop',
        authorization: [{
          actor: wax.userAccount,
          permission: 'active',
        }],
        data: {}
      },
      ...actions
    ]
  }, {
    blocksBehind: 3,
    expireSeconds: 1200,
  })
}

const claimResults = async ({ claims, wax }) => {
  const actions = await Promise.all(claims.map(async (claim) => {
    const claimableIndices = claim.claimables.map((c, idx) => c.is_claimed === 0 ? idx : null).filter(idx => idx !== null)

    return {
      account: 'colonizepool',
      name: 'claim',
      authorization: [{
        actor: wax.userAccount,
        permission: 'active',
      }],
      data: {
        claim_id: claim.claim_id,
        claimable_indices: claimableIndices
      },
    }
  }))

  return await wax.api.transact({
    actions: [
      {
        account: 'res.pink',
        name: 'noop',
        authorization: [{
          actor: wax.userAccount,
          permission: 'active',
        }],
        data: {}
      },
      ...actions
    ]
  }, {
    blocksBehind: 3,
    expireSeconds: 1200,
  })
}

const UpgradesList = ({ groupedUpgrades, children }) => {
  if (_.keys(groupedUpgrades).length === 0) {
    return <Alert severity="info" sx={{ width: '300px' }}>
      You don't have enough cards. <br/>Please select different mission or rarity.
      </Alert>
  }

  return (
    <Box sx={{ margin: 'auto' }}>
      {
        _.toPairs(groupedUpgrades).map(
          ([templateId, group]) =>
            <Card key={templateId} style={{ padding: '0.5rem', width: '500px', marginBottom: '0.3rem', display: 'flex', justifyContent: 'space-between' }}>
              <div style={{ display: 'inline-block', margin: 'auto' }}>
                <span>{ _.get(group, '0.assets.length') } x { _.get(group, '0.assets.0.data.rarity') } { _.get(group, '0.assets.0.name') }</span>
              </div>
              <div>{ children([templateId, group]) }</div>
            </Card>
        )
      }
    </Box>
  )
}

const Upgrades = ({ upgrades, wax, onCraftClicked }) => {
  const [upgradeAmount, setUpgradeAmount] = useState({})

  const groupedUpgrades = useMemo(() => {
    return _.groupBy(upgrades, (upgrade) => upgrade.templateId)
  }, [upgrades])

  const selectedUpgrades = useMemo(() => {
    const filteredGroups = _.mapValues(groupedUpgrades, upgrades => {
      const templateId = _.get(upgrades, '0.templateId')
      const amountPerGroup = _.get(upgradeAmount, templateId, upgrades.length)
      return _.take(upgrades, amountPerGroup)
    })
    return _.flatten(_.values(filteredGroups))
  }, [upgrades, upgradeAmount])

  const craft = () => {
    onCraftClicked({ upgrades: selectedUpgrades })
  }

  const changeUpgradeAmount = (templateId, group, event) => {
    const newVal = Math.min(parseInt(event.target.value), group.length)
    setUpgradeAmount(prevGroups => ({ ...prevGroups, [templateId]: newVal }) )
  }

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
      <UpgradesList groupedUpgrades={groupedUpgrades}>
        {
          ([templateId, group]) => (
            <>
              <TextField
                style={{ width: '80px' }}
                size="small"
                type="number"
                value={_.get(upgradeAmount, templateId, group.length)}
                max={group.length} min={0} onChange={_.curry(changeUpgradeAmount)(templateId, group)}
              />
              <span style={{ fontSize: '2rem' }}>/</span>
              <TextField style={{ width: '80px' }} size="small" type="number" value={group.length} disabled />
            </>
          )
        }
      </UpgradesList>
      <Box sx={{ marginTop: '1rem' }}>
        {
          selectedUpgrades.length === 0 ? null :
            <Button variant="contained" color="secondary" onClick={craft}>Craft {selectedUpgrades.length} times (Fee 1 WAX)</Button>
        }
      </Box>
    </Box>
  )
}

const RARITY_SELECT_OPTIONS = [
  { value: 'Standard', label: 'Standard' },
  { value: 'Shadow', label: 'Shadow' },
  { value: 'Stainless Steel', label: 'Stainless Steel' },
  { value: 'Gold', label: 'Gold' }
]

const MISSION_SELECT_OPTIONS = [
  { value: 'missionone', label: 'Mission one' },
  { value: 'missiontwo', label: 'Mission two' },
  { value: 'missionthree', label: 'Mission three' },
  { value: 'missionfour', label: 'Mission four' }
]

const SelectUpgrades = ({ wax, onUpgradesSent }) => {
  const [upgrades, setUpgrades] = useState()
  const { enqueueSnackbar } = useSnackbar()
  const [rarity, setRarity] = useState(_.get(RARITY_SELECT_OPTIONS, '0.value'))
  const [mission, setMission] = useState(_.get(MISSION_SELECT_OPTIONS, '0.value'))
  const [loading, setLoading] = useContext(LoaderContext)

  useEffect(() => {
    const perform = async () => {
      setLoading(true)
      const assets = await getAssets({ userAccount: wax.userAccount, rarity, schemaName: mission })
      const upgrades = await getUpgrades({ wax, assets })
      setUpgrades(upgrades)
      setLoading(false)
    }

    perform()
  }, [wax.userAccount, rarity, mission])

  const changeRarity = (event) => {
    setRarity(event.target.value)
  }

  const changeMission = (event) => {
    setMission(event.target.value)
  }

  const onCraftClicked = async ({ upgrades }) => {
    try {
      await applyUpgrades({ upgrades, wax })
      onUpgradesSent({ upgrades })
    } catch (error) {
      const contractError = _.get(error, 'details.0.message', null)
      console.log(contractError)

      if (contractError.match(/overdrawn balance/i)) {
        enqueueSnackbar("Not enough wax balance to continue", { variant: 'error' })
      } else {
        enqueueSnackbar(error.message, { variant: 'error' })
      }
    }

  }

  return <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
    <Box sx={{ display: 'flex' }}>
      <Box sx={{ marginRight: '1rem' }}>
        <InputLabel id="mission-select-label">Mission</InputLabel>
        <Select label="Mission" value={mission} onChange={changeMission} labelId="mission-select-label">
          { MISSION_SELECT_OPTIONS.map(option => <MenuItem key={option.value} value={option.value}>{option.label}</MenuItem>) }
        </Select>
      </Box>
      <Box>
        <InputLabel id="rarity-select-label">Rarity</InputLabel>
        <Select label="Rarity" value={rarity} onChange={changeRarity} labelId="rarity-select-label">
          { RARITY_SELECT_OPTIONS.map(option => <MenuItem key={option.value} value={option.value}>{option.label}</MenuItem>) }
        </Select>
      </Box>
    </Box>
    <Box sx={{ marginTop: '1rem'}}>
      {
        upgrades ? <Upgrades upgrades={upgrades} wax={wax} onCraftClicked={onCraftClicked} /> : null
      }
    </Box>
  </Box>
}

const ClaimNfts = ({ claims, wax, onClaimed }) => {
  const claim = async () => {
    await claimResults({ claims, wax })
    onClaimed()
  }

  return <Paper
    sx={{ margin: 'auto', padding: '1rem', width: '500px', height: '100px', display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'space-between' }}
  >
    <Typography variant="h6">You have {claims.length} claims!</Typography>
    <Button color="secondary" variant="contained" onClick={claim}>Claim</Button>
  </Paper>
}

const WaitForClaimables = ({ upgrades, wax, onClaimed }) => {
  const [claimResults, setClaimResults] = useState({})
  const [loading, setLoading] = useContext(LoaderContext)

  const getClaimIdForUpgrade = (upgrade) => _.get(upgrade, 'assets.0.asset_id')

  const allReady = () => _.keys(claimResults).length === upgrades.length

  useEffect(() => {
    setClaimResults({})
    setLoading(true)

    let run = true

    const perform = async () => {
      while(run) {
        const claims = await getClaims({ rpc: wax.api.rpc, accountName: wax.userAccount })
        const claimsById = _.mapValues(_.groupBy(claims, claim => claim.claim_id), group => _.first(group))

        let results = {}

        for (const upgrade of upgrades) {
          const claimId = getClaimIdForUpgrade(upgrade)
          const claim = claimsById[claimId]
          if (claim !== undefined) {
            results[claimId] = claim
          }
        }

        setClaimResults(results)
        if(_.keys(results).length === upgrades.length) {
          run = false
          setLoading(false)
        }

        await sleep(5000)
      }
    }
    perform()

    return () => run = false
  }, [upgrades])

  if (allReady()) {
    const [successes, failures] = _.partition(upgrades, upgrade => claimResults[getClaimIdForUpgrade(upgrade)].claimables.length === 1)

    return <Box>
      {
        allReady() ? <Box>
          <Box sx={{ fontSize: '2rem', marginBottom: '1rem', display: 'flex', justifyContent: 'center'  }}>
            <Chip label={`${successes.length} successes`} color="success" sx={{ marginRight: '1rem' }} />
            <Chip label={`${failures.length} failures`} color="error" />
          </Box>
          <ClaimNfts claims={_.values(claimResults)} wax={wax} onClaimed={onClaimed} />
        </Box> : null
      }
    </Box>
  } else {
    return null
  }
}

const Main = ({ wax }) => {
  const [upgradesSent, setUpgradesSent] = useState()
  const [loading, setLoading] = useContext(LoaderContext)
  const [oldClaims, setOldClaims] = useState(null)

  useEffect(() => {
    const perform = async () => {
      setLoading(true)
      const claims = await getClaims({ rpc: wax.api.rpc, accountName: wax.userAccount })
      setOldClaims(claims)
      setLoading(false)
    }
    perform()
  }, [wax.userAccount])

  const onUpgradesSent = ({ upgrades }) => {
    setUpgradesSent(upgrades)
  }

  const onClaimed = () => {
    setOldClaims([])
  }

  if (oldClaims === null) {
    return null
  }

  if (oldClaims.length > 0) {
    return <ClaimNfts claims={oldClaims} wax={wax} onClaimed={onClaimed} />
  }

  if (upgradesSent) {
    return <WaitForClaimables upgrades={upgradesSent} wax={wax} onClaimed={() => setUpgradesSent(null)} />
  } else {
    return <SelectUpgrades wax={wax} onUpgradesSent={onUpgradesSent} />
  }
}

export default Main
