import _ from 'lodash'
import { useEffect, useState, useMemo, useContext } from 'react'
import Tooltip from '@mui/material/Tooltip'
import Typography from '@mui/material/Typography'
import Paper from '@mui/material/Paper'
import Box from '@mui/material/Box'
import Grid from '@mui/material/Grid'
import Slider from '@mui/material/Slider'
import { Done as DoneIcon, ErrorOutline as ErrorOutlineIcon  } from '@mui/icons-material'
import WellbeingIcon from '../../images/resources/wellbeing.webp'
import EngineeringIcon from '../../images/resources/engineering.webp'
import OxygenIcon from '../../images/resources/oxygen.webp'
import WaterIcon from '../../images/resources/water.webp'
import FoodIcon from '../../images/resources/food.webp'
import ScienceIcon from '../../images/resources/science.webp'
import FuelIcon from '../../images/resources/fuel.webp'
import PowerIcon from '../../images/resources/power.webp'
import DataIcon from '../../images/resources/data.webp'
import MedicineIcon from '../../images/resources/medicine.webp'
import SocialIcon from '../../images/resources/social.webp'
import RegolithIcon from '../../images/resources/regolith.webp'
import MartiaIcon from '../../images/resources/martia.webp'
import RepairsIcon from '../../images/resources/repairs.webp'
import ConstructionMaterialIcon from '../../images/resources/construction_material.webp'
import LogisticIcon from '../../images/resources/logistic.webp'
import { LoaderContext } from '../../components/Loader.js'
import BaseTable, { AutoResizer } from 'react-base-table'
import { performSimulation, ITEMS_CONFIG } from './resource_chain.js'
import { RARITY_STRENGTH, getCards } from './cards.js'

function ResourceBalance ({ name, quantity, display = 'block', direction = 'row' }) {
  const icons = {
    'engineer_labor': EngineeringIcon,
    'wellbeing': WellbeingIcon,
    'oxygen': OxygenIcon,
    'water': WaterIcon,
    'food': FoodIcon,
    'science_labor': ScienceIcon,
    'fuel': FuelIcon,
    'power': PowerIcon,
    'data': DataIcon,
    'medicine': MedicineIcon,
    'regolith': RegolithIcon,
    'social_labor': SocialIcon,
    'martia': MartiaIcon,
    'repairs': RepairsIcon,
    'logistic_labor': LogisticIcon,
    'construction_materials': ConstructionMaterialIcon,
  }

  const src = icons[name]

  return <Box display={display} sx={{ margin: '0.3rem', minWidth: '2rem' }}>
    <div style={{ display: 'flex', alignItems: 'center', flexDirection: direction, textAlign: 'center' }}>
      <img style={{ margin: '0.2rem' }} alt={name} title={name} width="15px" height="15px" src={src} />
      <span>{quantity.toFixed(4)}</span>
    </div>
  </Box>
}

function ResourcesBalance({ resources, display = 'block', direction = 'row' }) {
  return _.entries(resources.getValues()).map(([name, quantity], idx) => <ResourceBalance key={idx} name={name} quantity={quantity} display={display} direction={direction} />)
}

function ItemsTable ({ items, itemsConfig, setItemsConfig }) {
  const balanceRenderer = ({ cellData }) => {
    return <ResourcesBalance resources={cellData} display="inline" direction="column" />
  }

  const inputRenderer = ({ rowData, cellData }) => {
    return <ResourcesBalance resources={rowData.usedResources} display="inline" direction="column" />
  }

  const outputRenderer = ({ rowData, cellData }) => {
    return <ResourcesBalance resources={rowData.producedResources} display="inline" direction="column" />
  }

  const settingsRenderer = ({ cellData, rowData }) => {
    return <Slider step={0.25} min={0} max={0.75} marks defaultValue={itemsConfig[rowData.name].slider} />
  }

  const columns = [
    { title: 'Name', key: 'name', dataKey: 'name', width: 300, resizable: true },
    { title: 'Input', key: 'requiredResources', width: 300, resizable: true, cellRenderer: inputRenderer },
    { title: 'Output', key: 'producedResources', width: 150, resizable: true, cellRenderer: outputRenderer },
    { title: 'Balance', key: 'currentResources', dataKey: 'currentResources', width: 1000, resizable: true, cellRenderer: balanceRenderer },
    // { title: 'Settings', key: 'settings', width: 1000, resizable: true, cellRenderer: settingsRenderer }
  ]
  const data = items.map((item, id) => ({ ...item, id }))

  return <AutoResizer>
    {
      ({ width, height }) => (
        <BaseTable data={data} width={width} height={ window.innerHeight - 200 } columns={columns} />
      )
    }
  </AutoResizer>

}

function SimulationResult ({ simulation, itemsConfig, setItemsConfig }) {
  return <Box sx={{ padding: '10px' }}>
    <Grid container spacing={2}>
      <Grid item xs={1}>
        <Paper sx={{ padding: '0.2rem' }}>
          <Typography sx={{ textAlign: 'center' }}>Resources</Typography>
          <ResourcesBalance resources={simulation.balance} />
        </Paper>
      </Grid>
      <Grid item xs={11}>
        <ItemsTable items={simulation.items} itemsConfig={itemsConfig} setItemsConfig={setItemsConfig} />
      </Grid>
    </Grid>
  </Box>
}

function ResourcesCalculator({ wallet }) {
  const [loading, setLoading] = useContext(LoaderContext)
  const [simulation, setSimulation] = useState()
  const [itemsConfig, setItemsConfig] = useState(ITEMS_CONFIG)

  useEffect(() => {
    if (!wallet) return

    setLoading(true)

    const req = async () => {
      const cards = await getCards({ wallet })
      const sim = performSimulation({ cards, itemsConfig })
      console.log(sim)
      setSimulation(sim)
      setLoading(false)
    }

    req()
  }, [wallet, itemsConfig])

  return simulation ? <SimulationResult simulation={simulation} itemsConfig={itemsConfig} setItemsConfig={setItemsConfig} /> : null
}

export default ResourcesCalculator
