import { useState } from 'react'
import { Box, Toolbar, Button, Backdrop, Typography, AppBar, Alert, Paper, Link } from '@mui/material'
import { Routes, Route, NavLink } from "react-router-dom"
import { createTheme, ThemeProvider } from '@mui/material/styles'
import ResourcesChainSimulation from './pages/resources_chain_simulation/ResourcesChainSimulation.js'
import BatchCrafter from './pages/batch_crafter/BatchCrafter.js'
import ExpeditionProfits from './pages/expedition_profits/ExpeditionProfits.js'
