import TextField from '@mui/material/TextField'
import Grid from '@mui/material/Grid'
import Typography from '@mui/material/Typography'

function AskWallet({ setWallet }) {
  return (
    <Grid
      container
      justify="center"
      alignItems="center"
      direction="column"
      spacing={5}
    >
      <Grid item>
        <Typography variant="h5" color="primary">
          Type your wax wallet and hit enter
        </Typography>
      </Grid>
      <Grid item>
        <TextField
          variant="outlined"
          label="Wax wallet"
          fullWidth
          style={{ marginBottom: "1em" }}
          onKeyPress={(e) => {
            if (e.key === "Enter") {
              setWallet(e.target.value)
            }
          }}
        />
      </Grid>
    </Grid>
  )
}

export default AskWallet
