import React, { useState } from 'react'
import { Box, Grid, Button, Container } from '@mui/material'
import * as waxjs from "@waxio/waxjs/dist"
import Main from './Main.js'

const wax = new waxjs.WaxJS({
  rpcEndpoint: 'https://wax.greymass.com',
  tryAutoLogin: true
})

const Login = ({ setUserAccount }) => {
  const login = async () => {
    const user = await wax.login()
    setUserAccount(user)
  }

  return (
    <Grid container direction="column" alignItems="center">
      <Grid item style={{ marginTop: '100px' }}>
        <Button size="large" variant="contained" color="secondary" onClick={login}>Login</Button>
      </Grid>
    </Grid>
  )
}

const App = () => {
  const [userAccount, setUserAccount] = useState()

  return <Container style={{ marginTop: '2rem' }}>
    {
      userAccount ?
        <Main userAccount={userAccount} wax={wax} /> :
        <Login setUserAccount={setUserAccount} />
    }
  </Container>
}

export default App
